















import { Component, Prop, Vue } from 'vue-property-decorator'
import bus from '@/assets/ts/bus'

@Component
export default class Tree extends Vue {
  private SLASH !:any
  @Prop() data !:any
  //展开文件夹
  private openFolder(obj :any) :void {
    obj.open = !obj.open
  }
  //打开文件编辑
  private openFile(path :string, name :string) :void {
    path = path + this.SLASH + name
    bus.$emit("onTreeSelectEvent", path)
  }
}
