




import { Component, Prop, Watch, Vue } from 'vue-property-decorator'
import exp from '@/assets/ts/editor.explain'

@Component
export default class AceEditor extends Vue {
  @Prop()
  editorId !:string
  @Prop({default: false})
  readOnly !:boolean
  @Prop({default: exp})
  content !:string
  @Prop({default: "monokai"})
  theme !:string
  @Prop({default: "text"})
  mode !:string
  @Prop({default: 4})
  tabSize !:number
  @Prop({default: 14})
  fontSize !:number

  aceOptions :object = {
    maxLines: null, // 最大行数，超过会自动出现滚动条
    minLines: null, // 最小行数，还未到最大行数时，编辑器会自动伸缩大小
    fontSize: this.fontSize, // 编辑器内字体大小
    theme: "ace/theme/" + this.theme, // 默认设置的主题
    mode: "ace/mode/" + this.mode, // 默认设置的语言模式
    tabSize: this.tabSize, // 制表符设置为 4 个空格大小
    readOnly: this.readOnly, //设置编辑器默认为可编辑
    enableSnippets: true, //开启代码块
    enableLiveAutocompletion: true,
    enableBasicAutocompletion: true,
    value: this.content
  }

  editor :any

  @Watch('content')
  valueChange(val :string, oldVal :string) {
    this.editor.setValue(val)
    this.editor.moveCursorTo(0, 0)
  }
  //初始化ace编辑器
  private initEditor() :void {
    let ace = require("ace-builds")
    require("ace-builds/webpack-resolver")
    require("ace-builds/src-noconflict/ext-language_tools")
    this.editor = ace.edit(this.editorId, this.aceOptions)
  }

  mounted() :void {
    this.initEditor()
  }

}
