






























import { Component, Prop, Vue } from 'vue-property-decorator'
import axios from 'axios'
import bus from '@/assets/ts/bus'
import CustomKeys from '@/assets/ts/shortcutKey'

import {menuBar, fileType} from '@/assets/ts/editor.conf'
import { sysConf, api } from '@/assets/ts/path.conf'

import AceEditor from '@/components/AceEditor.vue'
import Dialog from '@/components/DialogWindow.vue'
import Tree from '@/components/Tree.vue'


@Component({
  components: {
    Dialog,
    Tree,
    AceEditor
  }
})
export default class CodeEditorIDE extends Vue {
  private SLASH !:any

  isDialog :boolean = false
  menuSelect :number = -1 //菜单栏选中项
  menubar :object[] = menuBar //菜单栏项
  typeConf :any = fileType //文件类型配置
  fileTreeList :any = {} //文件树列表
  openFileGather :any = {} //所打开文件的集合
  openFilePathList :string[] = [] //打存放所有开的文件的路径
  selectedPath :string = '' //选中的文件路径
  path :string = sysConf.rootPath
  apiUrl :string = api.fileList


  //菜单栏
  private selectTimer(index :number) :void {
    this.menuSelect = index
  }
  //获取目录树
  private getFolderTree(folderPath :string) :void {
    axios.post(api.fileTree, {path: folderPath}).then((res :any) => {
      this.fileTreeList = res.data.data
    })
  }
  //获取文本
  private getFileContent(path :string) :void {
    if(this.openFileGather[path]) {
      this.selectedPath = path
      return
    }
    axios.post(api.fileContent, {path}).then((res :any) => {
      let tempArr = path.split(this.SLASH)
      let data = {name: tempArr[tempArr.length-1], path, content: res.data.data, mode: this.typeConf[res.data.suffix]}
      this.$set(this.openFileGather, path, data)
      this.selectedPath = path
      this.openFilePathList.push(path)
    })
  }

  //初始化快捷键
  private initShortcutKey() :void {
    CustomKeys.init({
      save: this.saveEditor,
      create: this.createEditor,
      open: this.openFolder
    })
  }
  //获取编辑内容
  private getEditorContent() :void {
    let ace = require("ace-builds")
    let n = this.openFilePathList.indexOf(this.selectedPath)
    let content = ace.edit('editor'+n).getValue()
    this.openFileGather[this.selectedPath].content = content
  }
  //保存
  private saveEditor() :void {
    this.getEditorContent()
    axios.post(api.saveFile, {file: this.openFileGather[this.selectedPath]}).then((res :any) => {
      console.log(res)
    })
  }
  //新建
  private createEditor() :void {
    console.log("创建文档")
  }
  //打开 --对话窗口
  private openFolder() :void {
    this.isDialog = true
  }
  //关闭窗口
  private closeDialog() :void {
    this.isDialog = false
  }

  //加载打开的文件
  private loadingFile(file :any) :void {
    this.openFileGather = {}
    this.openFilePathList = []
    this.selectedPath = ''
    if(file.type === 'folder') {
      this.getFolderTree(file.path)
    } else {
      this.getFileContent(file.path)
    }
  }
  //打开文件编辑
  public openFileEditor(path :string) :void {
    this.getFileContent(path)
  }

  //文件标签切换
  private openTagFile(file :any) :void {
    if(this.selectedPath === file.path) return
    this.selectedPath = file.path
  }
  //关闭文件
  private closeEdiotr(file :any, index :number) :void {
    this.getEditorContent()
    let tempPath :string = ''
    if(this.selectedPath === file.path) {
      if(index > 0) {
        tempPath = this.openFilePathList[index - 1]
      } else {
        tempPath = this.openFilePathList[1] || ''
      }
    } else {
      let n = this.openFilePathList.indexOf(this.selectedPath)
      tempPath = this.openFilePathList[n]
    }
    this.openFilePathList.splice(index, 1)
    Vue.delete(this.openFileGather, file.path)
    setTimeout(() => { //加个延时，不然关闭文件时无法跳转到另一个打开的文件
      this.selectedPath = tempPath
    },10)
  }

  mounted() :void {
    this.initShortcutKey()
    bus.$on('onTreeSelectEvent', (path :string) => {
      this.openFileEditor(path)
    })
  }
}
